.contactUs {
    padding-top: 60px;
    padding-bottom: 100px;
    background-image: linear-gradient(60deg, #96deda 0%, #50c9c3 100%);
}
.contactUs__section > h1 {
    padding-bottom: 30px;
}
.contact__right {
    width: 60%;
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
}
.contact__inputtText {
    display: flex;
    margin: auto;
    flex-direction: column;
    gap: 20px;
}
.contact__inputtText > input {
    padding: 10px;
    border: 2px solid #fff;
    background: transparent;
    outline: none;
    -webkit-transition: 1s;
    transition: 1s;
    
    
}
.contact__inputtText > input:focus {
    border: 2px solid orange;
}
.contact__inputTextarea > textarea{
    border: 2px solid #fff;
    background: transparent;
    outline: none;
    -webkit-transition: 1s;
    transition: 1s;
    
}
.contact_textarea:focus {
    border: 2px solid orange;
}
.contact__inputfield{
    display: flex;
    gap: 20px;
    
}
.contact__button {
    padding-top: 30px;
    display: flex;
    margin: auto;
    flex-direction: column;
}
.contact__button > p {
    padding-bottom: 20px;
    text-align: center;
    
}
.contactUs__logo > h1 {
    padding-left: 20px;
    
}
.contactUs__logo{
    display: flex;
    pointer-events: none;
    padding-bottom: 40px ;
}
.contactUs__logo > img {
    width: 10vh;
}

.footer {
    position: absolute;
    z-index: 1;
    right: 0;
    top: -1;
    padding-right: 30px;
}

.footer_a_tag {
    padding-left: 5px;
    text-decoration: none;
    color: aliceblue;
}
.footer_a_tag:hover{
    color: orange;
    -webkit-transition: 1s;
    transition: 1s;
}